// widgets spacing
$widget-bottom-margin: 15px;
$widget-bottom-padding: 20px;
$widget-inner-padding: 15px;
$widget-top-padding: 20px;

// widget color
$widget-background-color: transparent;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;
$widget-list-li-padding: 4px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: $default-text-color;
$widget-heading-color: $default-heading-color;

// links
$widget-link-color: $default-heading-color;
$widget-link-hover-color: $default-heading-color;

// specific widgets
$widget-search-form-height: 40px;
$widget-tag-cloud-height: 1em;

aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;
        border: 1px solid $default-border-color;
        border-radius: 2px;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .mkdf-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.mkdf-sidebar,
.wpb_widgetised_column {
    @import 'widgets/default-wordpress-widgets-boxed';
}