@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 30px 0 38px;
    }
	
	.mkdf-comment {
		
		&.mkdf-pingback-comment {
			
			.mkdf-comment-text {
                margin: 0;
                min-height: initial;
                padding: 15px 24px;
			}
            .mkdf-comment-arrow {
                display: none;
            }
		}
	}

    .mkdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 11px 0 0;
            padding: 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .mkdf-comment-text {
        position: relative;
        margin: 0 0 0 112px;
        min-height: 84px;
        border: 1px solid $default-border-color;
        padding: 15px 24px;

        .mkdf-comment-arrow {
            content: '';
            position: absolute;
            top: 34px;
            left: -9px;
            width: 15px;
            height: 15px;
            background-color: #fff;
            border-top: 1px solid $default-border-color;
            border-left: 1px solid $default-border-color;
            @include mkdfTransform(rotate(-45deg));
        }

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 6px 0 1px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            color: $default-heading-color;
            padding-left: 5px;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            margin: 2px 0 0;
            font-size: 12px;
            position: relative;
            display: inline-block;
        }

        .mkdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 14px;
            font-weight: 400;
            font-family: $default-text-font;
            color: $default-heading-color;

            &:hover {
                color: $first-main-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    border-top: 1px solid $default-border-color;
    
    .mkdf-comment-form-inner {
        padding: 36px 0 38px;
    }
    
    .comment-reply-title {
        margin: 0 0 30px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {
	        
            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
            top: 0;
        }

        .mkdf-comment-text {
            margin: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

%input-style {
	width: 100%;
    margin: 0 0 25px;
    padding: 14px 16px 12px;
	font-size: 13px;
	line-height: 1em;
	font-weight: 300;
	color: $default-text-color;
    font-family: $default-text-font;
	background-color: transparent;
	border: 1px solid $default-border-color;
	border-radius: 2px;
	outline: 0;
	-webkit-appearance: none;
	box-sizing: border-box;
	@include mkdfTransition(border-color 0.15s ease-out);
	
	&:focus {
		color: $default-heading-color;
		border-color: $default-text-color;
	}
	
	@include mkdfPlaceholder {
		color: inherit;
	}
}

#respond textarea {
    @extend %input-style;
}


.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include mkdfButtonDefaultStyle();
	@include mkdfButtonMediumParams();
	@include mkdfButtonSolidColor();
	cursor: pointer;
	
	&:hover {
		@include mkdfButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea,
.wpcf7-form-control.wpcf7-textarea {
	display: block;
    height: 200px;
}

.mkdf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

.mkdf-cf7-newsletter {
    position: relative;
    input[type='text'],
    input[type='email'] {
        height: 40px;
        line-height: calc(40px - 2px);
        padding: 0 50px 0 21px;
    }
    .mkdf-cf7-submit-newsletter {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 0;
        width: 40px;
        height: 32px;
        line-height: 32px;
        text-align: center;
        border-left: 1px solid $default-border-color;
        margin: 4px 0;
        color: #231f20;
        @include mkdfTransition(color .2s ease-in-out);
        &:after {
            content: "\e606";
            font-family: simple-line-icons;
            font-size: 12px;
            width: 100%;
            height: 100%;
            line-height: 32px;
            display: inline-block;
        }
        &:hover {
            color: $first-main-color;
        }
    }
    input.wpcf7-form-control.wpcf7-submit {
        background: transparent;
        cursor: pointer;
        width: 100%;
        height: 100%;
        border: 0;
        padding: 0;
        margin: 0;
        position: absolute;
        right: 0;
        top: 0;
        font-size: 0;
    }
    .ajax-loader {
        display: none !important;
    }
    div.wpcf7-response-output {
        margin: 0;
    }
}

.mkdf-cf7-newsletter-footer {
    @media only screen and (min-width: $phone-landscape-plus-pixel) and (max-width: $ipad-portrait) {
        width: 65%;
    }
    input[type='email'] {
        border-color: $default-border-color;
        color: #9c9c9c;
        width: calc(100% - 140px);
        padding: 0 12px;
        margin-right: 15px;
        height: 36px;
        line-height: 36px;

        @media only screen and (min-width: $ipad-portrait-plus-pixel) and (max-width: $ipad-landscape) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 10px;
        }

        &:focus {
            border-color: $default-border-color;
            color: #9c9c9c;
        }
    }

    .mkdf-cf7-email-newsletter-footer {
        position: relative;
        &:after {
            content: "\e086";
            font-family: simple-line-icons;
            font-size: 13px;
            width: 36px;
            height: 20px;
            line-height: 20px;
            position: absolute;
            right: 7px;
            top: 0;
            color: #fff;
        }
    }

    input[type='submit'] {
        width: 125px;
        padding: 8px 10px 4px;
        text-align: center;
        vertical-align: top;

        &:hover {
            color: $second-main-color;
        }
    }

    .ajax-loader {
        display: none !important;
    }
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;
    opacity: 0;
    @include mkdfTransition(opacity 0.15s ease);
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.mkdf-slider-is-moving {
		
		.owl-item {
			
			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.mkdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
			@include mkdfTransform(translateY(-50%));
			width: 50px;
			height: 50px;
			box-sizing: border-box;
			border: 1px solid #565656;
			border-radius: 50%;
			@include mkdfTransition(color .2s ease-out);
			
			> span {
				position: relative;
				display: block;
				vertical-align: middle;
				font-size: 26px;
				line-height: 48px;
                width: 48px;
                text-align: center;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 30px;
		}
		
		.owl-next {
			right: 30px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 42px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 8px;
				height: 8px;
				margin: 0 8px;
				border-radius: 3em;
				background-color: #d7d7d7;
				box-sizing: border-box;
				@include mkdfTransition(background-color .2s ease-out, transform .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					@include mkdfTransform(scale(1.25));
					background-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            img {
                border: 2px solid transparent;
                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.mkdf-404-page {

    &.mkdf-wrapper {
        padding: 0 !important;
    }
    
    .mkdf-content {
        height: 100vh;
	
	    .admin-bar & {
		    height: calc(100vh - 32px);
	    }
    }

    .mkdf-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .mkdf-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        padding: 0 20%;
	    text-align: center;

        .mkdf-404-title {
            margin: 55px 0 0;
            font-size: 160px;
            line-height: 0.85em;
            font-weight: 600;
            letter-spacing: 0;
        }

        @include phone-portrait {
            .mkdf-404-title {
                font-size: 100px;
            }
        }
	
	    .mkdf-404-subtitle {
            margin: 0;
            font-size: 35px;
            font-weight: 600;
            letter-spacing: -0.05em;
        }
	    
	    .mkdf-404-text {
		    margin: 30px 0 0;
            font-size: 18px;
            font-family: $default-heading-font;
            font-weight: 300;
	    }

        .mkdf-btn {
            margin: 45px 0 0;
	
	        &.mkdf-btn-light-style {
		        &:hover {
			        color: #fff !important;
		        }
	        }
        }
    }

    @include ipad-portrait {

        .mkdf-page-not-found {
            padding: 40px 30px;
        }
    }
	
	@include phone-landscape {
		
		.mkdf-content {
			height: auto;
			margin: 0 !important;
			
			.admin-bar & {
				height: auto;
			}
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 37px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include mkdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 37px;
        height: 37px;
        line-height: 41px;
        font-size: 18px;
        text-align: center;
        color: #a7a7a7;
        background-color: transparent;
        border: 1px solid #a7a7a7;
        overflow: hidden;
        border-radius: 3px;
        @include mkdfTransition(all 0.2s ease-in-out);

        &:hover {
            color: $second-main-color;
            border-color: $second-main-color;
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
	
    .mkdf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */