/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    background-color: $widget-background-color;
    margin: 0 0 $widget-bottom-margin;
    padding: $widget-top-padding $widget-inner-padding $widget-bottom-padding $widget-inner-padding;
    box-sizing: border-box;

    .mkdf-widget-title-holder {
        margin-bottom: $widget-list-item-padding;

        .mkdf-widget-title {
            color: $widget-heading-color;
            margin-bottom: 3px;
            margin-top: 0;
        }

        .mkdf-separator {
            border-color: $widget-text-color;
            border-bottom-width: 3px;
            width: 30px;
        }
    }

    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-li-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            border-top: 1px solid $widget-list-item-border-color;
            margin-top: $widget-list-item-padding;
        }
    }

    // widgets with lists and animation on link hover
    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {

        .mkdf-widget-title {
            margin-bottom: 0;
        }

        ul {

            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    color: $widget-link-color;

                    @include mkdfTransition(padding .2s ease);
                }
            }
        }
    }

    // widgets with lists and w/o animation on link hover
    &.widget_recent_comments,
    &.widget_rss {

        .mkdf-widget-title {
            margin-bottom: 0;
        }
    }

    // calendar
    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: $widget-text-color;
            background-color: $widget-list-item-background-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // rss
    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    // search
    &.widget_search {
        padding: 0;
        border:0;

        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 2px;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 13px;
                font-weight: 300;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include mkdfTransition(color .2s ease-in-out);
            }

            input.search-field {
                width: calc(100% - #{$widget-search-form-height});
                padding: 0 0 0 21px;

                @include mkdfPlaceholder {
                    color: $widget-text-color;
                    font-weight: 300;
                }
            }

            button {
                background: $widget-list-item-background-color;
                color: #231f20;
                cursor: pointer;
                font-size: 12px;
                width: $widget-search-form-height;
                top: 4px;
                height: 32px;
                line-height: 35px;
                border-left: 1px solid $default-border-color;


                @include mkdfTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    width: 100%;


                    &:before {
                        display: block;
                        line-height: inherit;
                        top: -3px;
                        position: relative;
                    }
                }
            }

            input[type="search"]::-webkit-search-decoration,
            input[type="search"]::-webkit-search-cancel-button,
            input[type="search"]::-webkit-search-results-button,
            input[type="search"]::-webkit-search-results-decoration {
                display: none;
            }
        }
    }

    // combobox
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: 500;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include mkdfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    // tag cloud
    &.widget_tag_cloud {

        a {
            background: $widget-background-color;
            @include mkdfBorderRadius(3px);
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-weight: 300;
            line-height: $widget-tag-cloud-height;
            margin: 0 6px 10px 0;
            text-transform: capitalize;
            font-size: 12px !important;
            padding: 10px 21px 8px;
            border: 1px solid $widget-list-item-border-color;

            &:hover {
                color: $first-main-color;
                border-color: $first-main-color;
            }
        }
    }

    &.mkdf-mortgage-calculator-widget,
    &.mkdf-recently-viewed-property-widget {
        padding: 0;

        .mkdf-widget-title-holder {
            margin: 0;
        }
    }

    &.mkdf-contact-property-widget {
        padding: 18px 14px 17px;
        box-sizing: border-box;
    }
}